body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.app{
  display: -ms-flexbox;
  display: flex;
  /*
     We are defining the max height of the app so that the editor knows how big to be
     currently the editor will take up whatever space it can if it is not constrained in its parent
   */
  min-height: 100vh;
  max-height: 100vh;
  width: 100%;
}
.app__main {
  max-width: 100%;
  max-height: 100vh;
  overflow: auto;
  -ms-flex-positive: 1;
      flex-grow: 1;
}



